.button {
    justify-content: center;
    border-radius: 16px;
    border: 1px solid #c7b6fc;
    box-shadow: 0px -4px 16px 0px rgba(94, 48, 232, 0.5) inset;
    background-color: #7c54f7;
    display: flex;
    gap: 10px;
    padding: 8px 16px;
  }
  .circle{
    display: flex;
    aspect-ratio: 1;
    flex-direction: column;
    justify-content: center;
    margin: auto 0;
  }
  .circle-item {
    border-radius: 50%;
    box-shadow: 0px 0px 16px 0px #a5f754;
    background-color: #a5f754;
    display: flex;
    aspect-ratio: 1;
    justify-content: center;
    align-items: center;
    width: 10px;
    height: 10px;
    padding: 0 1px;
  }
  .circle-shadow {
    border-radius: 50%;
    box-shadow: 0px 0px 16px 0px #6bf754;
    background-color: #6bf754;
    height: 8px;
  }
  .button-text {
    color: #f1f1f1;
    text-align: center;
    font: 700 16px/150% PP Neue Montreal, -apple-system, Roboto, Helvetica,
      sans-serif;
  }