.card-number{
    font-size: clamp(0.75rem, 5vw, 1.5rem);
    line-height: 1.5;
    font-weight: 500;
}

.card-title{
    font-size: clamp(0.75rem, 5vw, 1.5rem);
    line-height: 1.5;
    font-weight: 700;
}

.card-text{
    font-size: clamp(1.5rem, 5vw, 3rem);
    line-height: 1.5;
    font-weight: 500;
}

.card-top-shadow{
    box-shadow: 0px -52px 64px 0px rgba(28, 28, 28, 0.25);
}