/* * HEADER */
.header{
	width: clamp(328px, 40vw, 580px);
}
.header-title {
	font-size: clamp(3rem, 5vw, 4rem);
	line-height: 1.5;
	font-weight: 700;
}
.header-trademark {
	font-size: clamp(0.5rem, 5vw, 1rem);
	margin-top: clamp(0.7rem, 1.3vw, 1.25rem);

	font-weight: 400;
}
.header-img {
	width: 100%;
	height: auto;
}
.header-text{
	font-size: clamp(1.5rem, 5vw, 3rem);
	line-height: 1.5;
	font-weight: 500;
}
.header-circle-span{
	background-image: url(/src/icons/circle.svg);
	overflow: visible;
    display: inline-block;
}